.header {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #f3c54c;
  background: url('../../assets/images/books.webp') no-repeat center;
  height: 400px;
  background-size: cover;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 600;
  font-size: 3rem;
  line-height: 1.167;
  letter-spacing: -0.01562em;
  margin: 0 0 30px;
  color: var(--main);
}

.filters {
  display: flex;
  gap: 10px;
}

.button:hover {
  color: var(--main);
  cursor: pointer;
}

.imageLink {
  font-size: 8px;
  align-self: flex-end;
}

@media (max-width: 768px) {
  .header {
    /* background: url('../../assets/images/books.webp') no-repeat center; */
    height: 320px;
  }

  .title {
    font-size: 2.5rem;
    margin: 0 0 20px;
  }

  .filters {
    flex-direction: column;
    gap: unset;
    margin-top: 6px;
  }
}

@media (max-width: 425px) {
  .content {
    padding: 20px;
  }

  .title {
    font-size: 2rem;
  }
}
