.card {
  display: flex;
  flex-direction: column;
  width: 170px;
  cursor: pointer;
  height: 100%;
}

.card:hover {
  box-shadow: 1px 2px 6px 0px darkgray;
}

.image {
  height: 256px;
  width: 100%;
  background-color: lightgrey;
}

.textBlock {
  display: flex;
  flex-direction: column;
}

.author {
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1;
  color: darkgrey;
}

.title {
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  max-height: 36px;
}

.category {
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1;
  /* переделать везде по типу color="text.secondary" */
  color: var(--main) !important;
  margin-top: 6px !important;
}

@media (max-width: 425px) {
  .card {
    flex-direction: row;
    width: 320px;
  }
  
  .image {
    height: 220px;
    width: 158px;
  }

  .image img {
    height: 220px;
    width: 158px;
  }

  .textBlock {
    align-self: center;
  }
}

@media (max-width: 375px) {
  .card {
    width: 280px;
  }

  .image {
    height: 200px;
    width: 136px;
  }

  .image img {
    height: 200px;
    width: 136px;
  }
}
